/* oswald-regular - cyrillic */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald"),
    url("../fonts/oswald-v29-cyrillic-regular.woff2") format("woff2"),
    url("../fonts/oswald-v29-cyrillic-regular.woff") format("woff");
}

/* oswald-500 - cyrillic */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: local("Oswald"),
    url("../fonts/oswald-v29-cyrillic-500.woff2") format("woff2"),
    url("../fonts/oswald-v29-cyrillic-500.woff") format("woff");
}
